
section {
  padding-top: 10px;
  overflow: hidden;
}
section header {
  width: 100%;
  height: 24px;
  box-sizing: border-box;
  padding: 0 1.12rem;
}
section header h2 {
  display: inline-block;
  float: left;
  font-size: 1rem;
}
section header .more {
  float: right;
  font-size: 0.9rem;
  color: #42bd56;
}
section .section-content {
  margin-bottom: 20px;
}
section .section-content .items {
  padding: 15px 0 43px 0;
  overflow: hidden;
  width: 780px;
  height: 150px;
}
section .section-content .items .item {
  float: left;
  margin-left: 0.48rem;
  text-align: center;
}
section .section-content .items .item .item-title {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100px;
  margin-top: 0.6rem;
  line-height: 0.94rem;
  word-wrap: normal;
  color: #111;
}
section .section-content .items .item .rating {
  color: #aaa;
  line-height: 0.94rem;
  margin-top: 0.3rem;
  font-size: 0.72rem;
}
section .section-content .items .item .rating .rating-stars {
  display: inline-block;
  height: 10px;
}
section .section-content .items .item .rating .rating-stars .full,
section .section-content .items .item .rating .rating-stars .grey {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-size: 10px 10px;
}
section .section-content .items .item .rating .rating-stars .full {
  background-image: url("../../assets/rating-star-small-full.png");
}
section .section-content .items .item .rating .rating-stars .grey {
  background-image: url("../../assets/rating-star-small-grey.png");
}
section .section-content .items .item .rating .rating-average {
  color: #aaa;
  font-size: 0.72rem;
}


