
.search-res .search-hd {
  padding: 7px 20px;
  border-bottom: 1px solid #f2f2f2;
  overflow: hidden;
}
.search-res .search-hd input {
  height: 30px;
  box-sizing: border-box;
  background: #f5f5f5;
  border-radius: 3px;
  padding: 5px 8px;
  border: 0;
  outline: 0;
  -webkit-appearance: none;
  width: 85%;
  font-size: 14px;
  color: #111;
}
.search-res .search-hd a {
  float: right;
  color: #333;
  display: inline-block;
  width: 10%;
  height: 30px;
  line-height: 30px;
  text-decoration: none;
  font-size: 14px;
}
.search-res .search-ct > .title {
  padding: 0 18px;
}
.search-res .search-ct > ul {
  padding: 0 18px;
}
.search-res .search-ct > ul > span {
  text-align: center;
  display: block;
  color: #aaa;
  padding: 50px;
  margin-left: -20px;
}
.search-res .search-ct > ul .search-module {
  margin-top: 30px;
  border-bottom: 1px solid #f2f2f2;
  overflow: hidden;
}
.search-res .search-ct > ul .search-module > span {
  font-size: 15px;
  line-height: 15px;
  color: #aaa;
}
.search-res .search-ct > ul .search-module > a {
  display: block;
  overflow: hidden;
  font-size: 15px;
  padding: 10px 0;
  text-align: center;
}
.search-res .search-ct > ul ul li {
  border-bottom: 1px solid #f2f2f2;
  overflow: hidden;
}
.search-res .search-ct > ul ul li > a {
  display: block;
  overflow: hidden;
}
.search-res .search-ct > ul ul li > a img {
  float: left;
  padding: 10px 0px;
  width: 40px;
}
.search-res .search-ct > ul ul .subject-info {
  display: block;
  padding: 10px 0;
  overflow: hidden;
}
.search-res .search-ct > ul ul .subject-info > span {
  display: block;
  padding-left: 10px;
  font-size: 17px;
  color: #494949;
}
.search-res .search-ct > ul ul .subject-info > p {
  padding-left: 10px;
  -webkit-margin-after: 0px;
  -webkit-margin-before: 3px;
}
.search-res .search-ct > ul ul .subject-info > p > span {
  color: #aaa;
  font-size: 12px;
  height: 14px;
  vertical-align: middle;
}


